#summary {
    margin: 3rem auto 3rem auto;
    padding: 2rem;
    width: 90%;
    max-width: 90rem;
    border-radius: 16px;
    background: #ff9114bd;
    position: relative;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    color: white;
  }
  
#summary h2 {
    font-size: 3.5rem;
    margin-bottom: 1.25rem;
}
  

#summary article {
  border-radius: 12px;
  text-align: right;
}

#summary article img {
  width: 12rem;
  height: 12rem;
  border-radius: 12px;
  border: 5px solid white;
  margin-bottom: 1rem;
  transform: rotate(90deg);
}

#summary .origin {
  font-weight: bold;
  font-size: 1.5rem;
}

#summary time {
  font-weight: bold;
}

.main {
  width: 90%;
  max-width: 90rem;
  margin: 3rem auto;
  background-color: white;
  padding: 1rem;
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.main img {
  display: block;
  margin: 0 auto;
  max-width: 80%;
  transform: rotate(90deg);
}

.tag {
  background-color: white;
  color: #390281;
  padding: 0.5rem 1rem;
  margin: 1rem 1rem;
  border-radius: 8px;
}
